@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* A common best practice is to define custom properties on the :root pseudo-class,
so that it can be applied globally across your HTML document:
you use the custom property value by specifying your custom property name inside the var() function,
in place of a regular property value:
https://developer.mozilla.org/en-US/docs/Web/CSS/Using_CSS_custom_properties
*/
:root {
  --green: #58b4ae;
  --yellow: #ffe277;
  --orange: #ffb367;
  --pink: #ffe2bc;
  --white: #fff;
}

body {
  font-size: 25px;
  font-family: 'Inconsolata', monospace;
  background-color: #000;
  margin-left: 20px;
}

h1 {
  color: #ffe277;
  color: var(--yellow);
}

section {
  color: #fff;
  color: var(--white);
}

button {
  color: #fff;
  color: var(--white);
  font-family: inherit;
  font-weight: bolder;
  font-size: 12px;
  margin: 20px 20px 20px 0;
  padding: 10px 20px;
  background-color: #58b4ae;
}

.start-btn {
  font-size: inherit;
}

p {
  color: #fff;
  color: var(--white);
}

img {
  margin-bottom: 20px;
}

.hidden {
  display: none;
}

